  color: #888;
}
#content-footer .footer-meta {
  padding-right: 30px;
}
#content-footer .footer-return-top {
  text-align: center;
  position: absolute;
  top: -3px;
  right: 5px;
  width: 25px;
  height: 25px;
  background-color: #eaeaea;
  -webkit-border-radius: 3px;
  border-radius: 3px;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}
#content-footer .footer-return-top span {
  position: relative;
  top: 3px;
