/*==================================================================
  Radio/Checkboxes
-===================================================================*/
.admin-form .option {
  position: relative;
  padding-right: 15px;
  display: inline-block;
  vertical-align: middle;
}
.admin-form .option > input {
  position: absolute;
  height: inherit;
  width: inherit;
  opacity: 0;
  left: 0;
}
.admin-form .checkbox,
.admin-form .radio {
  cursor: pointer;
  position: relative;
  margin-right: 5px;
  background: #fff;
  display: inline-block;
  border: 3px solid #DDD;
  height: 21px;
  width: 21px;
  top: 4px;
  -webkit-border-radius: 2px;
  border-radius: 2px;
}
.admin-form .checkbox:before,
.admin-form .radio:before {
  content: '';
  display: none;
}
.admin-form input:checked + .checkbox:before,
.admin-form input:checked + .radio:before {
  display: block;
}
.admin-form .checkbox:before {
  position: absolute;
  top: 4px;
  left: 3px;
  width: 6px;
  height: 3px;
  border: solid #888;
  border-width: 0 0 3px 3px;
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}
.admin-form input:checked + .checkbox,
.admin-form input:checked + .radio {
  border: 3px solid #888;
}
.admin-form .radio {
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -o-border-radius: 20px;
  border-radius: 20px;
}
.admin-form .radio:before {
  margin: 4px;
  width: 7px;
  height: 7px;
  background: #888;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -o-border-radius: 10px;
  border-radius: 10px;
}
/*==================================================================
  Radio/Checkboxes:hover state 
===================================================================*/
.admin-form input:not([disabled]):hover + .checkbox,
.admin-form input:not([disabled]):hover + .radio {
  border-color: #666;
}
/*==================================================================
  Radio/Checkboxes:focus state 
-===================================================================*/
.admin-form input:focus + .checkbox,
.admin-form input:focus + .radio {
  border-color: #666;
}
.admin-form input:focus + .radio:before {
  background: #666;
}
.admin-form input:focus + .checkbox:before {
  border-color: #666;
}
/*==================================================================
  Switches
===================================================================*/
.admin-form .switch {
  cursor: pointer;
  position: relative;
  padding-right: 10px;
  display: inline-block;
  margin-bottom: 5px;
  height: 26px;
}
.admin-form .switch > label {
  cursor: pointer;
  display: inline-block;
  position: relative;
  height: 25px;
  width: 58px;
  color: #fff;
  font-size: 10px;
  font-weight: bold;
  line-height: 20px;
  text-align: center;
  background: #D7D7D7;
  border: 2px solid #D7D7D7;
  text-transform: uppercase;
  font-family: Helvetica, Arial, sans-serif;
  -webkit-transition: 0.3s ease-out;
  -moz-transition: 0.3s ease-out;
  -o-transition: 0.3s ease-out;
  transition: 0.3s ease-out;
  -webkit-border-radius: 3px;
  border-radius: 3px;
}
.admin-form .switch > label + span {
  display: inline-block;
  padding-left: 5px;
  position: relative;
  top: -7px;
}
.admin-form .switch > label:before {
  content: attr(data-off);
  position: absolute;
  top: 1px;
  right: 3px;
  width: 33px;
}
.admin-form .switch > label:after {
  content: "";
  margin: 1px;
  width: 19px;
  height: 19px;
  display: block;
  background: #fff;
  -webkit-border-radius: 2px;
  border-radius: 2px;
}
.admin-form .switch > input {
  -webkit-appearance: none;
  position: absolute;
  width: inherit;
  height: inherit;
  opacity: 0;
  left: 0;
  top: 0;
}
/*==================================================================
  Switch:focus state
-===================================================================*/
.admin-form .switch > input:focus {
  outline: none;
}
.admin-form .switch > input:focus + label {
  color: #fff;
  border-color: #C7C7C7;
  background: #C7C7C7;
}
.admin-form .switch > input:focus + label:after {
  background: #fff;
}
/*==================================================================
  Switch:checked state
===================================================================*/
.admin-form .switch > input:checked + label {
  border-color: #999;
  background: #999;
  padding-left: 33px;
  color: white;
}
.admin-form .switch > input:checked + label:before {
  content: attr(data-on);
  left: 1px;
  top: 1px;
}
.admin-form .switch > input:checked + label:after {
  margin: 1px;
  width: 19px;
  height: 19px;
  background: white;
}
/*==================================================================
  Switch:checked:focus state
-===================================================================*/
.admin-form .switch > input:checked:focus + label {
  background: #555;
  border-color: #555;
}
/* Round Style*/
.admin-form .switch-round > label {
  -webkit-border-radius: 13px;
  -moz-border-radius: 13px;
  -o-border-radius: 13px;
  border-radius: 13px;
}
.admin-form .switch-round > label + span {
  top: -2px;
}
.admin-form .switch-round > label:before {
  width: 33px;
}
.admin-form .switch-round > label:after {
  width: 19px;
  color: #D7D7D7;
  content: "\2022";
  font: 20px/20px Times, Serif;
  -webkit-border-radius: 13px;
  -moz-border-radius: 13px;
  -o-border-radius: 13px;
  border-radius: 13px;
}
.admin-form .switch-round > input:checked + label {
  padding-left: 33px;
}
.admin-form .switch-round > input:checked + label:after {
  color: #999;
}
/*==================================================================
  Switch - Custom Success/Danger Styled
-===================================================================*/
.admin-form .switch-custom > label {
  background: #f3a79b;
  border-color: #f3a79b;
}
.admin-form .switch-custom > input:hover + label,
.admin-form .switch-custom > input:focus + label {
  background: #e74b31;
  border-color: #e74b31;
}
/* Checked state */
