  background: #70ca63;
  border-color: #70ca63;
}
.admin-form .switch-custom > input:checked:hover + label,
.admin-form .switch-custom > input:checked:focus + label {
  background: #47a839;
  border-color: #47a839;
}
/*==================================================================
